                                        // let flag = isInputType
                                        //     ? item.itemtype !== 'number'
                                        //         ? foolValue.target
                                        //             ? foolValue.target.isFlag
                                        //             : false
                                        //         : false
                                        //     : false;
                                        // if (!flag && item.maxlength && isInputType && valueChange && foolValue) {

